
import bootstrapCommonBase from '../../styles/bootstrap/base/common';
import materialCommonBase from '../../styles/material/base/common';

import {iconStyle as bootstrapIconStyle, iconScript as bootstrapIconScript, iconUnscript as bootstrapIconUnscript} from '../../styles/bootstrap/soul/icon';
import {iconStyle as materialIconStyle, iconScript as materialIconScript, iconUnscript as materialIconUnscript} from '../../styles/material/soul/icon';
import {Theming} from '@tinijs/core';

import {LitElement, html} from 'lit';
import {property} from 'lit/decorators.js';
import {classMap, ClassInfo} from 'lit/directives/class-map.js';
import {styleMap} from 'lit/directives/style-map.js';
import {ColorsAndGradients, Sizes} from '@tinijs/core';

export const ICON = 'icon';
export const TINI_ICON = `tini-${ICON}`;

@Theming({
  styling: {bootstrap:[bootstrapCommonBase,bootstrapIconStyle],material:[materialCommonBase,materialIconStyle]},
  scripting: {bootstrap:{script:bootstrapIconScript,unscript:bootstrapIconUnscript},material:{script:materialIconScript,unscript:materialIconUnscript}},
})
export class TiniIconComponent extends LitElement {
  static readonly defaultTagName = TINI_ICON;

  @property({type: String}) declare src?: string;
  @property({type: String}) declare color?: ColorsAndGradients;
  @property({type: String}) declare size?: Sizes;

  private mainClasses: ClassInfo = {};
  protected willUpdate() {
    this.mainClasses = {
      [ICON]: true,
      recolor: !!this.color,
      [`color-${this.color}`]: !!this.color,
      [`size-${this.size}`]: !!this.size,
    };
  }

  protected render() {
    return html`
      <i
        part=${ICON}
        class=${classMap(this.mainClasses)}
        style=${styleMap({'--icon-image': `url(${this.src})`})}
      ></i>
    `;
  }
}
