
import bootstrapCommonBase from '../../styles/bootstrap/base/common';
import materialCommonBase from '../../styles/material/base/common';

import {buttonStyle as bootstrapButtonStyle, buttonScript as bootstrapButtonScript, buttonUnscript as bootstrapButtonUnscript} from '../../styles/bootstrap/soul/button';
import {buttonStyle as materialButtonStyle, buttonScript as materialButtonScript, buttonUnscript as materialButtonUnscript} from '../../styles/material/soul/button';
import {Theming} from '@tinijs/core';

import {LitElement, html} from 'lit';
import {property} from 'lit/decorators.js';
import {classMap, ClassInfo} from 'lit/directives/class-map.js';
import {ColorsAndGradients, Colors, Sizes, JustifyContents} from '@tinijs/core';

export const BUTTON = 'button';
export const TINI_BUTTON = `tini-${BUTTON}`;

@Theming({
  styling: {bootstrap:[bootstrapCommonBase,bootstrapButtonStyle],material:[materialCommonBase,materialButtonStyle]},
  scripting: {bootstrap:{script:bootstrapButtonScript,unscript:bootstrapButtonUnscript},material:{script:materialButtonScript,unscript:materialButtonUnscript}},
})
export class TiniButtonComponent extends LitElement {
  static readonly defaultTagName = TINI_BUTTON;

  @property({type: String}) declare color?: ColorsAndGradients;
  @property({type: String}) declare textColor?: Colors;
  @property({type: String}) declare size?: Sizes;
  @property({type: String}) declare justify?: JustifyContents;
  @property({type: Boolean, reflect: true}) declare disabled?: boolean;

  constructor() {
    super();
    this.disabled = false;
  }

  private mainClasses: ClassInfo = {};
  protected willUpdate() {
    this.mainClasses = {
      [BUTTON]: true,
      [`bg-${this.color}`]: !!this.color,
      [`color-${this.textColor}`]: !!this.textColor,
      [`size-${this.size}`]: !!this.size,
      [`justify-${this.justify}`]: !!this.justify,
    };
  }

  protected render() {
    return html`<button
      part=${BUTTON}
      class=${classMap(this.mainClasses)}
      ?disabled=${this.disabled}
    >
      <slot></slot>
    </button>`;
  }
}
