
import bootstrapCommonBase from '../../styles/bootstrap/base/common';
import materialCommonBase from '../../styles/material/base/common';

import {boxStyle as bootstrapBoxStyle, boxScript as bootstrapBoxScript, boxUnscript as bootstrapBoxUnscript} from '../../styles/bootstrap/soul/box';
import {boxStyle as materialBoxStyle, boxScript as materialBoxScript, boxUnscript as materialBoxUnscript} from '../../styles/material/soul/box';
import {Theming} from '@tinijs/core';

import {LitElement, html} from 'lit';
import {property} from 'lit/decorators.js';
import {classMap} from 'lit/directives/class-map.js';
import {ColorsAndGradients, Colors} from '@tinijs/core';

export const BOX = 'box';
export const TINI_BOX = `tini-${BOX}`;

@Theming({
  styling: {bootstrap:[bootstrapCommonBase,bootstrapBoxStyle],material:[materialCommonBase,materialBoxStyle]},
  scripting: {bootstrap:{script:bootstrapBoxScript,unscript:bootstrapBoxUnscript},material:{script:materialBoxScript,unscript:materialBoxUnscript}},
})
export class TiniBoxComponent extends LitElement {
  static readonly defaultTagName = TINI_BOX;

  @property({type: String}) declare background?: ColorsAndGradients;
  @property({type: String}) declare textColor?: Colors;
  // @property({type: String}) declare padding?: Multipliers;
  // @property({type: String}) declare margin?: Multipliers;

  private mainClasses = {};
  protected willUpdate() {
    this.mainClasses = {
      [BOX]: true,
      [`bg-${this.background}`]: !!this.background,
      [`color-${this.textColor}`]: !!this.textColor,
    };
  }

  protected render() {
    return html`
      <div part=${BOX} class=${classMap(this.mainClasses)}>
        <slot></slot>
      </div>
    `;
  }
}
